@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 400;
  src: local('Inter'),
        local('Inter-Regular');
  src: url('../fonts/Inter-Regular.woff2') format("woff2"),
       url('../fonts/Inter-Regular.woff') format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 900;
  src: local('Inter'),
       local('Inter-Black');
  src: url('../fonts/Inter-Black.woff2') format("woff2"),
       url('../fonts/Inter-Black.woff') format("woff");
}

@font-face {
  font-family: 'Inter';
  font-style:  normal;
  font-weight: 500;
  src: local('Inter'),
       local('Inter-Medium');
  src: url('../fonts/Inter-Medium.woff2') format("woff2"),
       url('../fonts/Inter-Medium.woff') format("woff");
}
